import { Component } from '@angular/core';
import { ActionSheetController  ,AlertController, IonicPage,ModalController, NavController, NavParams, InfiniteScroll } from 'ionic-angular';
import { Auth ,NotificationProvider } from '../../../../providers';
import { TranslateService } from '@ngx-translate/core';
import { Storage } from '@ionic/storage';
import { pdfx } from '../../../../providers/pdf/pdfx';


@IonicPage()
@Component({
  selector: 'page-Unidadacademica3-list',
  templateUrl: 'unidadacademica3-list.html',
})
export class Unidadacademica3ListPage {
  public loading: boolean = true;
  items: any=[];
  items2: any=[];
    generated = '';
    public hasFilter: boolean = false;
    idtipo: any;
    ntipo: any;
    idempresa:any;
    idafiliado:any='na';

    permisom: any=[];
    potros1: any=[];
    pingreso:boolean=false;
    pconsulta:boolean=false;
    peliminacion:boolean=false;
    pcreacion:boolean=false;
    pactualizacion:boolean=false;
    preporte:boolean=false;
    pfiltro:boolean=false;
    jsonObject2 :any;


  public gLimit: number = 50;
  public PageSegmet: string = 'foods';
  public gSearchQuery: string = '';
  username: string = "";
//private barcodeScanner:BarcodeScanner,
  constructor( public Pdfx:  pdfx,  public actionSheetCtrl: ActionSheetController,public alertCtrl: AlertController,public storage: Storage, public msj: NotificationProvider, public translateService: TranslateService
    ,public navCtrl: NavController,
    private _MODAL  		: ModalController, public navParams: NavParams,
     public regService: Auth

    ) {
      this.translateService.get('APPX').subscribe((value) => {
        this.idempresa=value

    })

    this.permisom = String(navParams.get("permiso")).split(",");
    this.potros1 = JSON.parse(JSON.stringify(navParams.get("potros1")))
    this.pingreso=(  this.permisom[0] == 'true');
    this.pconsulta=(  this.permisom[1] == 'true');
    this.peliminacion=(  this.permisom[2] == 'true');
    this.pcreacion=(  this.permisom[3] == 'true');
    this.pactualizacion=(  this.permisom[4] == 'true');
    this.pfiltro=(  this.permisom[5] == 'true');
    this.preporte=(  this.permisom[6] == 'true');

    var someJsonString = navParams.get("potros1");

    // jsonObject will contain a valid JavaScript object
  if(someJsonString!='')
  {
   this.jsonObject2 = JSON.parse(someJsonString);
  }
  }


  public dapermisos(data,opcion){
    var re:any[];
    if(data)
{
    for (var i = 0; i < data.length; i++)
    {
      if(data[i].modulo==opcion)
      {
          re=data[i].permisos
        break;

      }

    }

  }
  //DD/MM/YYYY hh:mm A
  return re;

 }



 public dapermisos2(data,opcion){
  var re='';
  if(data)
  {
  for (var i = 0; i < data.length; i++)
  {
    if(data[i].modulo==opcion)
    {
        re=data[i].permisos.ingreso + ',' + data[i].permisos.consulta+ ',' + data[i].permisos.elimina+ ',' + data[i].permisos.creacion+ ',' + data[i].permisos.actualiza+ ',' + data[i].permisos.filtro+ ',' + data[i].permisos.reporte
      break;

    }

  }
  }
  return re;
}

 public dapermisos_visible(opcion){
  var re:boolean=false;
  if(this.jsonObject2)
  {
  for (var i = 0; i < this.jsonObject2.length; i++)
  {
    if(this.jsonObject2[i].modulo==opcion)
    {
        re=!this.jsonObject2[i].permisos.ingreso
      break;

    }

  }
  }

//DD/MM/YYYY hh:mm A
return re;

}


  public clearSearchg(): void {
    this.gSearchQuery = '';
  }
  showFilter()
  {




  }



  pideregistros() {
      let self = this;
      self.loading = true;
      this.regService.getReg(this.idtipo + '/todos','unidadacademica3s').then((data) => {
        this.items=data;
        this.items2=data;
        self.loading = false;
  }, (err) => {
    this.msj.showError3('Error',err.error);
   // this.translateService.get('NO_OPERACION').subscribe((value) => { this.msj.showError3('Error',value);  })
  });
  }
  ngOnInit() {
    this.storage.get('username').then((value) => {
      this.username = value;
  });
  }
  ionViewDidLoad() {
    this.idtipo=this.navParams.data.record._id;
    this.ntipo=this.navParams.data.record.nombre;
    this.pideregistros();
  }

  public loadMore(ev: InfiniteScroll) {
    this.gLimit += 50;
    setTimeout(() => ev.complete(), 1000);
  }


  deleteRecord(item : any, no) : void
  {

    let alert = this.alertCtrl.create({ title: 'Esta seguro de realizar esta operaciÃ³n?',
    buttons: [
      {text: 'Si',
        handler: () => {

          let recordID 		: string		= item._id;
          this.regService.deleteReg(recordID+'/'+this.username+ '/' + this.idempresa + '/' + this.idafiliado,'unidadacademica3s').then((data) => {
              this.items.splice(no, 1);
              this.items = this.items.concat([]);
              this.items2=this.items
            }, (err) => {
              this.msj.showError3('Error',err.error);
          });

        }
      },
      {
        text: 'No',  handler: () => { }
      }
    ]
  });
  alert.present();




  }
  value:any;

  updateRecord(item : any,no) : void
   {
    let addModal = this._MODAL .create('Unidadacademica3CreatePage', { record : item,record2:{idtipo:this.idtipo,ntipo:this.ntipo} },{showBackdrop: true,enableBackdropDismiss: false});
    addModal.onDidDismiss(data => {
      if (data) {
       this.pideregistros();
      }
    })
    addModal.present();
    //  this.navCtrl.push('EmpresaCreatePage', { record : item });
   }
   addRecord() : void
   {
    let addModal = this._MODAL .create('Unidadacademica3CreatePage', { record : null ,record2:{idtipo:this.idtipo,ntipo:this.ntipo}},{showBackdrop: true,enableBackdropDismiss: false});
    addModal.onDidDismiss(data => {
      if (data) {
        this.pideregistros();
      }
    })
    addModal.present();

      //this.navCtrl.push('EmpresaCreatePage');
   }


   conferenciaRecord3(item : any) : void
   {
    this.navCtrl.push('Unidadperiodo3ListPage', { record : item  ,  tnombre:  item.ididioma.nombre + ' ' + item.idtipogrupo.nombre
    ,tunidad:this.ntipo  ,permiso:this.dapermisos2(this.jsonObject2,'CURSOS'),potros1:this.navParams.get("potros1"),record2:{idtipo:this.idtipo,idnombre:this.ntipo,idtipo2:item._id,idnombre2:item.nombre,idcodigo2:item.codigo}});
   }

   conferenciaRecord(item : any) : void
   {

    this.navCtrl.push('Unidadnivel3ListPage', { record : item,  tnombre:  item.ididioma.nombre + ' ' + item.idtipogrupo.nombre
    ,unidad:item.idtipounidad.nombre });




   }

   conferenciaRecordD(item : any) : void
   {

    this.navCtrl.push('Unidadpago3ListPage', { record : item });




   }

   conferenciaRecordA(item : any) : void
   {






   }



   conferenciaRecordC(item : any) : void
   {

    this.navCtrl.push('Unidaddia3ListPage', { record : item });




   }



}



